import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { EkomiWidgetService } from '../../services/ekomi-widget.service';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'quantion-ekomi-rank-widget',
  templateUrl: './ekomi-rank-widget.component.html',
  styleUrls: ['./ekomi-rank-widget.component.scss']
})
export class EkomiRankWidgetComponent implements OnInit, AfterViewInit {

  @Input() v: string;
  @Input() locale: string;
  @Input() id: string;

  constructor(
    private ekomiWidgetService: EkomiWidgetService
  ) { }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    this.ekomiWidgetService.loadScriptRankWidget(this.v, this.locale);
  }

}
