import { Component, OnInit, Input, AfterViewInit, OnDestroy } from '@angular/core';
import { EkomiWidgetService } from '../../services/ekomi-widget.service';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'quantion-ekomi-smart-widget',
  templateUrl: './ekomi-smart-widget.component.html',
  styleUrls: ['./ekomi-smart-widget.component.scss']
})
export class EkomiSmartWidgetComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() customerId: string;
  @Input() language: string;
  @Input() widgetTokens: string;
  // tslint:disable-next-line: no-inferrable-types
  @Input() draftMode: boolean = false;

  constructor(
    private ekomiWidgetService: EkomiWidgetService
  ) { }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    this.ekomiWidgetService.loadSmartWidget(this.customerId, this.language, this.widgetTokens, this.draftMode);
  }

  ngOnDestroy(): void {
    this.ekomiWidgetService.removeSmartWidgetScript();
  }

}
